<template>
  <v-app-bar fixed :extended="extended" extension-height="310" dark>
    <v-app-bar-nav-icon @click="extended = !extended"></v-app-bar-nav-icon>
    <template child-flex v-if="extended" #extension>
      <v-list dense rounded>
        <v-list-item link to="/" @click="extended = false">
          <v-list-item-icon>
            <v-icon large>home</v-icon>
          </v-list-item-icon>
          <v-list-item-title>Home</v-list-item-title>
        </v-list-item>

        <v-list-item link to="/trips" @click="extended = false">
          <v-list-item-icon>
            <v-icon large>directions_car</v-icon>
          </v-list-item-icon>
          <v-list-item-title>Trips</v-list-item-title>
        </v-list-item>

        <v-list-item link to="/expenses" @click="extended = false">
          <v-list-item-icon>
            <v-icon large>payment</v-icon>
          </v-list-item-icon>
          <v-list-item-title>Expenses</v-list-item-title>
        </v-list-item>

        <v-list-item link to="/claims" @click="extended = false">
          <v-list-item-icon>
            <v-icon large>attach_money</v-icon>
          </v-list-item-icon>
          <v-list-item-title>Claims</v-list-item-title>
        </v-list-item>

        <v-list-item link to="/profile" @click="extended = false">
          <v-list-item-icon>
            <v-icon large>settings</v-icon>
          </v-list-item-icon>
          <v-list-item-title>Settings</v-list-item-title>
        </v-list-item>
      </v-list>
    </template>

    <v-toolbar-title>EZ EXPENSES</v-toolbar-title>

    <div class="flex-grow-1"></div>

    <v-btn text color="grey lighten-1" @click="logout"
      >Logout <v-icon right>exit_to_app</v-icon></v-btn
    >
  </v-app-bar>
</template>

<script>
export default {
  name: "MobileNav",
  data() {
    return {
      extended: false
    };
  },
  methods: {
    logout() {
      this.$store
        .dispatch("logout")
        .then(() => this.$router.push({ name: "login" }))
        .catch(error => {
          this.$store.dispatch("setMessage", error.response);
        });
    }
  }
};
</script>

<style>
.v-list {
  width: 100%;
}
</style>
